this.mainFormData.reportMonth +
"'",
params = {
flag: 0,
params: '',
type: 1,
sql: sql
},
_this = this
this.util.post(url, params, function(res) {
if (res) {
_this.mainFormData.periodsNum =
_this.mainFormData.reportMonth +
'' +
(res.count + 1) +
''
}
})
}
